 <template>
  <el-card>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/datas' }">数据列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 卡片视图 -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="请输入内容"
                    @keyup.enter.native="getDatalist()"
                    clearable
                    v-model="characteristic">
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="getDatalist()"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <div class="grid-content bg-purple"></div>
        </el-col>
      </el-row>
      <!-- 列表 -->
      <vxe-table border
                 resizable
                 :data="datas"
                 :edit-config="{trigger: 'click', mode: 'cell'}"
                 @edit-closed="editClosedEvent">
        <vxe-table-column type="seq"
                          field="id"
                          width="60"></vxe-table-column>
        <vxe-table-column field="characteristic"
                          title="特征标识"
                          :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
        <vxe-table-column field="message"
                          title="数据"
                          :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
        <vxe-table-column field="creationtime"
                          title="创建时间">
          <template v-slot="{ row }">
            <el-date-picker v-model="row.creationtime"
                            type="datetime"
                            value-format="timestamp"
                            @change="handleChange(row,'creationtime')"
                            placeholder="选择日期时间">
            </el-date-picker>
          </template>
        </vxe-table-column>
        <vxe-table-column field="status"
                          title="处理状态"
                          :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
        <vxe-table-column field="updatetime"
                          title="更新时间">
          <template v-slot="{ row }">
            <el-date-picker v-model="row.updatetime"
                            type="datetime"
                            value-format="timestamp"
                            @change="handleChange(row,'updatetime')"
                            placeholder="选择日期时间">
            </el-date-picker>
          </template></vxe-table-column>
        <vxe-table-column field="retrycount"
                          title="处理次数">
          <template v-slot="{ row }">
            <el-input-number v-model="row.retrycount"
                             :min="0"
                             size="mini"
                             @change="handleChange(row,'retrycount')"
                             label="描述文字">
            </el-input-number>
          </template>
        </vxe-table-column>
        <vxe-table-column field="exceptionmsg"
                          title="异常信息"
                          :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>

      </vxe-table>

      <!-- 分页区域 -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="current"
                     :page-sizes="[5, 10, 50, 100]"
                     :page-size="size"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-card>
  </el-card>
</template>
<script>
export default {
  data () {
    return {
      datas: [],
      // 总页数
      total: 0,
      // 每页调速
      size: 5,
      // 当前第几页
      current: 1,
      // 搜索关键字
      characteristic: null
    }
  },
  created () {
    this.getDatalist()
  },
  methods: {
    async getDatalist () {
      const { data: ras } = await this.$http.post('/zjs-transaction/queryPage',
        {
          page: { current: this.current, size: this.size },
          where: { characteristic: this.characteristic === '' ? null : this.characteristic }
        })
      console.log(ras)
      this.datas = ras.body.records
      this.total = ras.body.total
    },
    handleSizeChange (newSize) {
      this.size = newSize
      this.current = 1
      this.getDatalist()
    },
    handleCurrentChange (newCurrent) {
      this.current = newCurrent
      this.getDatalist()
    },
    async editClosedEvent ({ row, column }, event) {
      console.log('object :', row.id)
      console.log('object :', row[column.property])
      const { data: ras } = await this.$http.post('/zjs-transaction/update',
        {
          id: row.id, [column.property]: row[column.property]
        })
      console.log(ras)
      this.getDatalist()
    },
    async  handleChange (row, column) {
      console.log('column :', row)
      console.log('object :', row.id)
      console.log(column)
      console.log(row[column])
      // var time = row[column]
      const { data: ras } = await this.$http.post('/zjs-transaction/update',
        {
          id: row.id, [column]: row[column]
        })
      console.log(ras)
      this.getDatalist()
    }
  }

}
</script>
 <style lang="less" scoped></style>
